<template>
    <div class="px-5 mx-5">
        <CommonComment :threadId="threadId" :showAll="true" />
    </div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { useRoute } from "vue-router";
export default {
    setup(props) {
        const route = useRoute();
        const state = reactive({
            threadId: route.query.threadId,
        });
        return {
            ...toRefs(state),
        };
    },
};
</script>

